.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cart-item {
  box-shadow: 0 2px 10px 0 rgb(0 0 0 / 12%);
}

.stream {
  margin: 1rem 0;
  display: flex;
  justify-content: center;
}

.stream iframe {
  border-radius: 5px;
}

.price {
  color: red;
}

.product-image {
  width: 60%;
  border-radius: 10px;;
}

